import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        mapLocation: {},
        goodsResult: {},
        historygoods: {},
        address: '',
    },
    mutations: {
        upt(state, result) {
            state.mapLocation = result;
        },
        up(state, result) {
            state.goodsResult = result;
        },
        uu(state, result) {
            state.historygoods = result;
        },
        pp(state, result) {
            state.address = result;
        }

    },
    actions: {},
    modules: {}
})