			<van-tabbar-item replace to="/topic" icon="description">专题</van-tabbar-item>
			<van-tabbar-item replace to="/category" icon="apps-o">分类</van-tabbar-item>
			<van-tabbar-item replace to="/cart" icon="cart-o">购物车</van-tabbar-item>
